<template>
  <div>
    <!-- Use the component in the right place of the template -->
    <tiptap-vuetify
      :value="value"
      :extensions="extensions"
      :rules="[rules.required]"
      :placeholder="placeholder"
      :class="dark ? 'tip-tap-editor-dark' : ''"
      @input="$emit('input', arguments[0])"
    />
  </div>
</template>
<script>
// import the component and the necessary extensions
import {
  TiptapVuetify,
  Heading,
  Bold,
  Image,
  Italic,
  Strike,
  Underline,
  Code,
  Paragraph,
  BulletList,
  OrderedList,
  ListItem,
  Link,
  Blockquote,
  HardBreak,
  HorizontalRule,
  History
} from 'tiptap-vuetify'
import { mapState } from 'vuex'
import validations from '@/mixins/validations'
export default {
  components: { TiptapVuetify },
  mixins: [validations],
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: 'Body'
    }
  },
  data: (vm) => ({
    extensions: [
      History,
      Blockquote,
      Link,
      Underline,
      Strike,
      Italic,
      Image,
      ListItem,
      BulletList,
      OrderedList,
      [
        Heading,
        {
          options: {
            levels: [1, 2, 3]
          }
        }
      ],
      Bold,
      Link,
      Code,
      HorizontalRule,
      Paragraph,
      HardBreak
    ]
  }),
  computed: {
    ...mapState('settings', {
      dark: (state) => state.settings.theme.theme.dark
    })
  }
}
</script>

<style lang="scss">
.tip-tap-editor-dark {
  .v-toolbar__content {
    background-color: #1e1e1e !important;
    width: 100%;
  }
  .tiptap-vuetify-editor__content {
    color: white !important;
  }
}
</style>
